import React, {Component} from 'react';
import '../../css/Main/AboutUs.css';

export default class AboutUs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false
    }
  }

  openToggle = () => {
    this.setState({
      isOpen: !this.state.isOpen
    })
  };

  render() {
    return (
      <section className={`about-us ${this.state.isOpen ? 'about-us_open' : ''}`}>
        <h2 className="about-us__title">  
          <br/>  - BosaNoga!</h2>
        <p className="about-us__text">
               ,  .         .
        </p>
        <h3 className="about-us__text_header">    :</h3>
        <ol className="about-us__text">
          <li>  .      -     -         .         ,   .
          </li>
          <li>       ,    .         -  BosaNoga.
          </li>
          <li>      . ,    : -,   -      - ,       .
          </li>
          <li>     .</li>
          <li>       .</li>
        </ol>
        <p className="about-us__text">
             ,        ,      .
        </p>
        <p className="about-us__text">
               , ,    , ,   .     -,        -,   -.     ,       .</p>
        <span className="about-us__text_overlay"/>
        <button className="about-us__text_button" onClick={this.openToggle}>{`${this.state.isOpen ? '' : ''}`}</button>
      </section>
    );
  }
}



